@import url(https://fonts.googleapis.com/css2?family=Merriweather&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Merriweather', serif;
  line-height: 150%;
}

.title, .search-form {
  display: flex;
  justify-content: center;
  padding: 10px;
}

.search-topic {
  display: flex;
  justify-content: center;
  /* width: 400px; */
  align-items: center;
}
.search-bar {
  border: none;
  /* background-color: #c2e9fb; */
  border-bottom: 1px solid #888888;
  /* border-radius: 4px; */
  padding: 4px;
  font-size: 16px;
  width: 150px;
}

.search-age, .search-gender {
  /* display: flex; */
  justify-content: center;
  /* align-items: center; */
  padding: 10px;
}
.buttons-gender {
  padding-top: 10px;
}
#radio1, #radio2 {
  visibility: hidden;
}
label {
  cursor: pointer;
  background-color: #c2e9fb;
  padding: 4px;
  border-radius: 4px;
}
.search-gender input:checked + label {
  background-color: rgb(110, 156, 241);
  color: white;
}

.topic-section {
  padding-top: 3rem;
}
.topic-container {
    display: flex;
    flex-direction: row;

    justify-content: center;
    align-content: center;
    padding: 10px;
}

.topic-card {
    background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
    box-shadow: 4px 4px 10px rgba(70, 2, 13, 0.2);
    width: 400px;
    text-decoration: none;
    border-radius: 4px;
    padding: 1rem;
}

a:link, a:visited {
    text-decoration: none;
    color: #333333;
}
